
@import url("https://fonts.googleapis.com/css2?family=Josefin+Sans:wght@500&family=Roboto:wght@100&display=swap");

html,
body {
  font-family: "Josefin Sans", sans-serif;
  background-color: #e7e1e1;
  color: #373737;
}
p {
  font-size: 1.2vw;
}

h1 {
  text-transform: uppercase;
  font-family: "Josefin Sans", sans-serif;
  font-size: 3vw
}

.mainH1 {
  text-align: left;
  margin-bottom: 20%;
}

.mainH1 p {
  font-size: 2vw;
}

.mock {
  margin-left: 10px;
  width: 15%;
}

.content {
  font-size: 1vw;
  padding-top: 1%;
  padding-right: 20%;
  background-image: linear-gradient(#c7b2da, #9787aa);
  border-bottom-left-radius: 50%;
  border-bottom-right-radius: 50%;

  box-shadow: 18px 18px 36px #c4bfbf, -18px -18px 36px #ffffff;
}

.profilePic {
  /* height: 40vw; */
  height: 28vw;
}

.compPic {
  height: 9.2vw;
}
.compText {
  margin-top: 5%;
  font-size: 1.5vw;
  color: #373737;
}

.content h1 {
  margin-top: 7%;
  font-size: 2.8vw;
}

.header {
  padding-left: 20%;
  display: flex;
  margin-top: 2%;
}

.fadeText {
  margin-top: 5%;
}

.header b {
  color: #856084;
}

#link img {
  width: 3em;
  display: none;
}

#links{
  color: #fff8f8;
}

#link {
  height: 1vw;
  display: block;
  width: 90%;
  text-align: right;
  font-size: 1.1vw;
  letter-spacing: 0.2vw;
  background-color: #a38bba;
  padding: 2%;
  border-radius: 0 4em 4em 0;
  position: fixed;
  transition: all 0.5s ease;
  z-index: 3;
}

#link:hover {
  cursor: pointer;
}

#link a:current {
  color: purple;
}

#menuLink{
  color: #fff8f8;
  text-decoration: underline;
  letter-spacing: 0.23vw;
  
  text-decoration: none;
}

#menuLink:hover{
cursor: pointer;
text-decoration: underline;
}

#homeLink{
  color: #fff8f8;
  text-decoration: underline;
  letter-spacing: 0.23vw;
  
  text-decoration: none;
}

#homeLink:hover{
cursor: pointer;
text-decoration: underline;
}

#tonedown{
  color: #acb2ce;
  text-decoration: none;

}

#tonedown:hover {
  color: #acb2ce;
  text-decoration: none;
  cursor: default;


}

.comp {
  margin-top: 5%;
  margin-left: 5%;
  margin-right: 5%;
  margin-bottom: 2%;
}
.proj {
  display: grid;
  grid-template-columns: 80% auto;
  letter-spacing: 0.12vw;
}

button {
  border-radius: 5em;
  background: #e7e1e1;
  box-shadow: 18px 18px 36px #c4bfbf, -18px -18px 36px #ffffff;
  border: none;
  transition: all 0.5s ease;
  letter-spacing: 0.01vw;
  font-size: 1.2vw;
  font-family: "Josefin Sans", sans-serif;
}
button:hover {
  letter-spacing: 0.12vw;
  border-radius: 5em;
  background: linear-gradient(145deg, #d0cbcb, #f7f1f1);
  box-shadow: 20px 20px 60px #c4bfbf, -20px -20px 60px #ffffff;
  cursor: pointer;
}
.comp img {
  padding: 1%;
}

.mock1 {
  width: 15%;
}

.c {
  margin: 2%;
  width: 40%;
  height: 40%;
  background-image: linear-gradient(#bfc5e0, #afb5d1);
  border-radius: 20%;
  border-top-right-radius: 50%;
  padding: 6%;
}

.mock_img {
  margin: 2%;
  width: 100%;
}

.mock_img img {
  width: 15%;
  height: auto;
  padding-left: 7%;
  padding-right: 7%;
}

.Project {
  background-color: #d6caca;
}

.photoGridc1 {
  display: flex;
  width: 100%;
  margin-top: 5%;
  background-color: #f9f7f7;
}
.photoGridc1 img {
  width: 20%;
}
.photoGridc2 img {
  width: 20%;
}
.photoGridc2 {
  display: flex;
  width: 100%;
}

.aboutMe {
  padding-left: 5%;
  padding-right: 5%;
  margin-top: -3%;
  background-color: #f9f7f7;

  /*background-color: #d8d4d4;*/
  /*box-shadow: inset 8px 8px 16px #c5c1c1, inset 8px 8px 16px #c5c1c1;*/
}


#divide {
  background-image: url("../img/divide.png");
  background-repeat: repeat;
  background-color: rgb(249, 247, 247);
  height: 175px;
}
#education {
  background-image: url("../img/divide2.png");
  background-repeat: repeat;
  background-color: rgb(249, 247, 247);
  height: 175px;
}

#contact-me {
  background-image: url("../img/divide3.png");
  background-repeat: repeat;
  background-color: #C4BFBF;
  height: 175px;
}


.aboutMe h1 {
  padding-top: 5%;
  text-shadow: 1px 1px white, -1px -1px #444;
  transition: all 1s ease;
}

.bounceH1{
  letter-spacing: 2vw;
}

#aboutcontent {
  display: flex;
  width: 100%;
  row-gap: 10px;
}

#aboutcontent ul {
  letter-spacing: 0.15vw;
  margin-left: -4%;
}

#aboutcontent img {
  width: 20%;
  margin-left: 20%;
  border-bottom-left-radius: 4em;
  border-bottom-right-radius: 1em;
  border-top-right-radius: 8em;
  border-top-left-radius: 2em;
  padding-bottom: 2em;
}



#educ {
  padding-left: 5%;
  padding-right: 5%;
  padding-bottom: 5%;
}

#educontent {
  display: grid;
  grid-template-columns: 50% 20% 30%;
  letter-spacing: 0.12vw;
}
#educontent img {
  margin-left: 20%;
  width: 50%;
  margin-top: -10%;
}

#contact {
  margin-top: -1%;
  margin-bottom: -5%; 
  padding:5%;
  background-color: #C4BFBF;
}

#contactcontent {
  letter-spacing: 0.12vw;
}

#contactcontent a{
  text-decoration: none;
  color: #856084;
}
#contactcontent a:hover{
  text-decoration: underline;
  color: #614660;
}

#FreeLink{
  
  margin-top: 1%;
  margin-left: 1%;
}

#FreeLink a{
  color: #373737;
  text-decoration: none;
}


#FreeLink  a:hover{
  color: #7c7c7c;
}

@media screen and (max-width: 768px) {

#divide{
  display: none
}

#education{
  display: none;
}

#contact-me{
  display: none;
}



#link{
  width: 25%;
  display: block;
  padding:2%;
  padding-top:3%;
  margin-top: -2%;
  text-align: left;

}
#educ{
  padding-top: 2%;
}

.proj{
  padding-top: 2%;
  padding-bottom: 2%;
}

a{
  font-size: 1.1vw;
  color: wheat;
}

#link:hover{
  cursor:default;
}

#link  a:hover{
  cursor:pointer;
}

  }